import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
  Table,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";
import Classes from "./product.module.css";

const Warranty = (props) => {
  const [allWaranty, setAllWaranty] = useState([]);
  const [values, setValues] = useState({
    title: "",
    label: "",
  });
  const [submitLoading, setSubmitLoading] = useState(false);

  const handleState = (e) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    if (values.title === "") {
      toast.warn("فیلد نام گارانتی نباید خالی باشد");
      return false;
    }

    setSubmitLoading(true);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            mutation addWaranty($name:String!,$label:String){
                waranty(name:$name,label:$label){
                  status,
                  message
                }
              }
            `,
        variables: {
          name: values.title,
          label: values.label,
        },
      },
    })
      .then((res) => {
        setSubmitLoading(false);
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { message, _id } = res.data.data.waranty;
          toast.success(message);
          setAllWaranty((prev) => [
            ...prev,
            { _id: _id, name: values.title, label: values.label },
          ]);
          setValues({ title: "", label: "" });
        }
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query allWaranty($page:Int,$limit:Int){
            getAllWaranty(page:$page,limit:$limit){
              name,
              label
            }
          }
              `,
        variables: {
          page: 1,
          limit: 30,
        },
      },
    })
      .then((res) => {
        setSubmitLoading(false);
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          const { getAllWaranty } = res.data.data;
          setAllWaranty(getAllWaranty);
        }
      })
      .catch((err) => console.log(err));
  }, [values.label, values.title]);

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>اضافه کردن گارانتی محصول </CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="title">عنوان گارانتی محصول</Label>
                    <Input
                      type="text"
                      placeholder="عنوان گارانتی محصول را وارد نمایید"
                      name="title"
                      value={values.title}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="label">توضیح گارانتی محصول</Label>
                    <Input
                      type="text"
                      placeholder="توضیحات گارانتی محصول را وارد نمایید"
                      name="label"
                      value={values.label}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>{" "}
              </FormGroup>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmit}
              >
                {submitLoading ? <Spinner size="sm" /> : "ثبت گارانتی محصول"}
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>
              <Row>
                <Col xs="2">
                  <i className="fa fa-align-justify"></i>
                  <span style={{ marginRight: "5px" }}>همه گارانتی ها</span>
                </Col>
              </Row>
            </CardHeader>
            <CardBody>
              <Table striped responsive size="sm">
                <thead>
                  <tr>
                    <th>عنوان گارانتی محصول</th>
                    <th>توضیحات گارانتی محصول</th>
                    <th>عملیات</th>
                  </tr>
                </thead>
                <tbody>
                  {allWaranty.map((item) => (
                    <tr key={item._id}>
                      {" "}
                      <td>{item.name}</td>
                      <td>{item.label}</td>
                      <td>
                        <Button color="primary" style={{ marginLeft: "5px" }}>
                          {" "}
                          ویرایش
                        </Button>
                        <Button color="danger">حذف</Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Warranty;
